
.MapsToggler {
  position: fixed;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
  color: darkblue;
  font-size: 3em;
  z-index: 1;
  font-family: 'Alegreya', sans-serif;
  font-weight: 900;
}
.MapsToggler__lefty {
    left: 1rem;
}
.MapsToggler__righty {
    right: 1rem;
}
@media screen and (max-width: 900px) {
.MapsToggler__lefty {
      left: .5rem;
}
.MapsToggler__righty {
      right: .5rem;
}
}



