
.Imago {
  font-family: 'Alegreya', sans-serif;
  font-weight: 400;
  font-size: .9em;
  position: relative;
  cursor: pointer;
}
.Imago__a {
    background: #ffea25;
}
.Imago__b {
    background: #0063a9;
}
.Imago__c {
    background: #ff6f32;
}
.Imago__d {
    background: #93d64d;
}
.Imago__e {
    background: #ff3e00;
}
.Imago__f {
    background: #d17a94;
}
.Imago__g {
    background: #7eb4ff;
}
.Imago__h {
    background: #008a20;
}
.Imago__inline {
    display: inline-block;
    position: relative;
    padding: 0 .25em;
    margin: 0 .125em;
}
.Imago:hover .Imago--caption {
    display: block;
}
.Imago--caption {
    display: none;
    position: absolute;
    top: 3em;
    left: 0;
    width: 12rem;
    background: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    z-index: 1;
    font-family: 'Alegreya', sans-serif;
    font-size: .65em;
    padding: .5em;
}



