
ul, li {
  list-style: none;
}
.Maps {
  background: white;
  position: relative;
  height: 100%;
  width: calc(100% + 16px);
  padding-right: 5em;
  overflow-y: scroll;
  overflow-x: hidden;
}
.Maps--wrap {
    box-shadow: -2px 0 10px rgba(0, 0, 0, 0.4);
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.MapsWrapper {
  width: 100%;
  max-width: 800px;
  position: fixed;
  right: -800px;
  top: 0;
  bottom: 0;
  z-index: 10;
  background: white;
}
.MapsWrapper__open .Maps--wrap {
    -webkit-transform: translate3d(-800px, 0, 0);
            transform: translate3d(-800px, 0, 0);
}
.MapsWrapper__closed .Maps--wrap {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}



