
.MenuToggler {
  position: fixed;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
  color: darkred;
  font-size: 3em;
  font-family: 'Alegreya', sans-serif;
  font-weight: 900;
  z-index: 1;
}
.MenuToggler__lefty {
    left: .75rem;
}
.MenuToggler__righty {
    right: .75rem;
}
@media screen and (max-width: 900px) {
.MenuToggler__lefty {
      left: .5rem;
}
.MenuToggler__righty {
      right: .5rem;
}
}



