
.MapIcon {
  display: block;
  padding: .5em;
  margin: 1em 0;
  width: 1.6em;
  height: 1.6em;
  position: relative;
}
.MapIcon span {
    position: absolute;
    font-size: 2em;
    font-weight: 900;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -60%);
        -ms-transform: translate(-50%, -60%);
            transform: translate(-50%, -60%);
}
.MapIcon__a {
    background: #ffea25;
}
.MapIcon__b {
    background: #0063a9;
}
.MapIcon__c {
    background: #ff6f32;
}
.MapIcon__d {
    background: #93d64d;
}
.MapIcon__e {
    background: #ff3e00;
}
.MapIcon__f {
    background: #d17a94;
}
.MapIcon__g {
    background: #7eb4ff;
}
.MapIcon__h {
    background: #008a20;
}



