html, body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
body {
  font-size: 17px;
  line-height: 1.4;
  font-family: 'Cardo', serif;
  max-width: 1080px;
  margin: auto;
  background: whitesmoke;
}
@media screen and (max-width: 900px) {
body {
      font-size: 15px;
}
}
h1, h2, h3, h4 {
  font-family: 'Alegreya', sans-serif;
  font-weight: 900;
}
h4 {
  font-weight: 700;
}
blockquote {
  font-family: 'Averia', sans-serif;
}
img {
  max-width: 100%;
  height: auto;
}
p img {
  height: 2em;
  display: inline-block;
  width: auto;
  vertical-align: middle;
}
nav ul {
  padding-left: 1em;
  padding-top: .125em;
  padding-bottom: .25em;
}
nav li {
  font-size: 1.2em;
  line-height: 1;
}
nav li li {
    font-size: 0.9em;
}
main {
  font-size: 1.3em;
}
.References {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
  padding-left: 0;
  max-width: 75ch;
}
.Reference {
  width: calc(99.9% * 1/3 - (30px - 30px * 1/3));
  margin-bottom: 1em;
  font-family: 'JunicodeC', 'serif';
}
.Reference:nth-child(1n){
  float: left;
  margin-right: 30px;
  clear: none;
}
.Reference:last-child{
  margin-right: 0;
}
.Reference:nth-child(3n){
  margin-right: 0;
  float: right;
}
.Reference:nth-child(3n + 1){
  clear: both;
}
@media screen and (max-width: 900px) {
.Reference {
      width: calc(99.9% * 1/2 - (30px - 30px * 1/2));
}
.Reference:nth-child(1n){
    float: left;
    margin-right: 30px;
    clear: none;
  }
.Reference:last-child{
    margin-right: 0;
  }
.Reference:nth-child(2n){
    margin-right: 0;
    float: right;
  }
.Reference:nth-child(2n + 1){
    clear: both;
  }
}
@media screen and (max-width: 600px) {
.Reference {
      width: calc(99.9% * 1/1 - (30px - 30px * 1/1));
}
.Reference:nth-child(1n){
    float: left;
    margin-right: 30px;
    clear: none;
  }
.Reference:last-child{
    margin-right: 0;
  }
.Reference:nth-child(1n){
    margin-right: 0;
    float: right;
  }
.Reference:nth-child(1n + 1){
    clear: both;
  }
}
.Reference--name {
    font-weight: 700;
}
.Reference::before {
    font-style: normal;
    font-family: 'Alegreya', sans-serif;
}
.Reference--meta {
    font-style: italic;
    display: block;
}
.Reference__livre {
    position: relative;
}
.Reference__livre::before {
      content: "livre - ";
}
.Reference__site::before {
    content: 'site - ';
}
.Reference__article::before {
    content: 'article - ';
}
.Reference__documentaire::before {
    content: 'documentaire - ';
}
.Reference__interview::before {
    content: 'interview - ';
}
.Reference__publication::before {
    content: 'publication scientifique - ';
}
.Reference__conference::before {
    content: 'conf√©rence - ';
}
.Footnotes {
  font-family: 'JunicodeC', serif;
  font-size: .75em;
  max-width: 135ch;
  padding-left: 0;
  margin-left: 0;
}
.Footnotes li {
    margin-bottom: .25em;
}
.Partie__Cover p {
  font-family: 'JunicodeC', serif;
  font-size: .85em;
}
.Partie__Cover figure {
  padding: 0;
}
.Partie__Cover figure img {
    max-height: 55vh;
}
.Partie__Cover figure figcaption {
    font-size: .65em;
    font-style: italic;
}
sup {
  line-height: inherit;
  vertical-align: inherit;
  position: relative;
  top: -8px;
  font-size: .65em;
}
a {
  color: darkblue;
}
main {
  padding: 0 1.5em;
}
@media screen and (max-width: 900px) {
main {
      padding: 0 1em;
}
}
.Partie p {
  margin-bottom: 1.4em;
}
blockquote {
  padding-left: 0;
  margin: .5em 0;
}
h2 {
  margin-bottom: .25em;
}
h2 + h3 {
  margin-top: .25em;
}
h3 {
  margin-bottom: .25em;
}
h3 + h4 {
  margin-top: .25em;
}
blockquote {
  max-width: 68ch;
}
em {
  line-height: 1;
}
.Body--list {
  padding-left: 0;
  font-size: .8em;
  margin: 0;
  max-width: 90ch;
}
.Body--list__condensed {
    font-family: 'JunicodeC',monospace;
}
.Partie .nombot {
  margin-bottom: 0.5em;
}
article em {
  font-style: italic;
  font-family: 'Cardo', serif;
}



